@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* END RESET */

html {
  --color-green: #14bdac;
  --color-green-dark: #0da192;
  --color-gray: #30383f;
  --color-white: #ffffff;
  --font-family-main: "Roboto", sans-serif;
  --height-header: 80px;
  --padding-header: 20px;
  --border-px: 8px;
}

body {
  color: #30383f;
  font-family: "Roboto", sans-serif;
  font-family: var(--font-family-main);
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.wrapper {
  margin: 0 auto;
  width: 80%;
}

header p {
  margin-bottom: 16px;
}

.turn {
  text-transform: lowercase;
  font-style: italic;
}

.game {
  background-color: #14bdac;
  background-color: var(--color-green);
}

.grid {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 80px;
  margin-bottom: 80px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

table {
  border-collapse: collapse;
  border-spacing: 0px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

table,
td {
  padding: 20px;
}

td {
  height: 100px;
  width: 100px;
  font-size: 100px;
  font-weight: bold;
  text-align: center;
}

tr:first-child td:first-child {
  border-right: 8px solid #0da192;
  border-right: var(--border-px) solid var(--color-green-dark);
  border-bottom: 8px solid #0da192;
  border-bottom: var(--border-px) solid var(--color-green-dark);
}

tr:first-child td:last-child {
  border-left: 8px solid #0da192;
  border-left: var(--border-px) solid var(--color-green-dark);
  border-bottom: 8px solid #0da192;
  border-bottom: var(--border-px) solid var(--color-green-dark);
}

tr:nth-child(2) td:first-child {
  border-right: 8px solid #0da192;
  border-right: var(--border-px) solid var(--color-green-dark);
  border-bottom: 8px solid #0da192;
  border-bottom: var(--border-px) solid var(--color-green-dark);
}

tr:nth-child(2) td:nth-child(2) {
  border-top: 8px solid #0da192;
  border-top: var(--border-px) solid var(--color-green-dark);
  border-bottom: 8px solid #0da192;
  border-bottom: var(--border-px) solid var(--color-green-dark);
}

tr:nth-child(2) td:last-child {
  border-left: 8px solid #0da192;
  border-left: var(--border-px) solid var(--color-green-dark);
  border-bottom: 8px solid #0da192;
  border-bottom: var(--border-px) solid var(--color-green-dark);
}

tr:last-child td:first-child {
  border-right: 8px solid #0da192;
  border-right: var(--border-px) solid var(--color-green-dark);
}

tr:last-child td:last-child {
  border-left: 8px solid #0da192;
  border-left: var(--border-px) solid var(--color-green-dark);
}

button {
  padding: 10px 8px;
  background-color: #ffffff;
  color: #14bdac;
  color: var(--color-green);
  font-weight: bold;
  line-height: 28px;
  font-size: 16px;
  text-transform: uppercase;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: none;
  cursor: pointer;
  outline: none;
}

button:hover {
  /* background-color: var(--color-green);
  color: #ffffff; */
  opacity: 0.8;
}

#result {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-self: center;
          align-self: center;
  line-height: 40px;
  font-size: 60px;
  font-weight: bold;
  color: rgb(84, 84, 84);
  text-align: center;
}

#result span {
  display: block;
  padding-bottom: 16px;
}

.game-over {
  opacity: 0.1;
}

.emoji {
  margin-top: 32px;
}

header {
  padding: 60px;
  background-color: #ffffff;
  color: rgba(0, 0, 0, 0.54);
  text-align: center;
}

.flex-radio {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 40px;
}

.flex-radio div {
  padding: 8px;
}

